import * as THREE from 'three';

const textureUrls = [
	process.env.PUBLIC_URL + '/images/oberon_texture.jpeg', process.env.PUBLIC_URL + '/images/ariel_texture.jpeg', process.env.PUBLIC_URL + '/images/callisto_texture.jpeg', process.env.PUBLIC_URL + '/images/charon_texture.jpeg', process.env.PUBLIC_URL + '/images/comet_texture.png', process.env.PUBLIC_URL + '/images/deimos_texture.jpeg', process.env.PUBLIC_URL + '/images/dione_texture.jpeg', process.env.PUBLIC_URL + '/images/earth_clouds.png', process.env.PUBLIC_URL + '/images/earth_lights.png', process.env.PUBLIC_URL + '/images/earth_texture.jpg', process.env.PUBLIC_URL + '/images/enceladus_texture.jpeg', process.env.PUBLIC_URL + '/images/europa_texture.jpeg', process.env.PUBLIC_URL + '/images/ganymede_texture.jpeg', process.env.PUBLIC_URL + '/images/iapetus_texture.jpeg', process.env.PUBLIC_URL + '/images/io_texture.jpeg', process.env.PUBLIC_URL + '/images/jupiter_texture.jpeg', process.env.PUBLIC_URL + '/images/mars_texture.jpeg', process.env.PUBLIC_URL + '/images/mercury_texture.jpeg', process.env.PUBLIC_URL + '/images/mimas_texture.jpeg', process.env.PUBLIC_URL + '/images/miranda_texture.jpeg', process.env.PUBLIC_URL + '/images/moon_texture.jpeg', process.env.PUBLIC_URL + '/images/neptune_texture.jpeg', process.env.PUBLIC_URL + '/images/phobos_texture.jpeg', process.env.PUBLIC_URL + '/images/pluto_texture.jpeg', process.env.PUBLIC_URL + '/images/rhea_texture.jpeg', process.env.PUBLIC_URL + '/images/saturn_rings.png', process.env.PUBLIC_URL + '/images/saturn_texture.jpeg', process.env.PUBLIC_URL + '/images/star_texture_orange.jpeg', process.env.PUBLIC_URL + '/images/tethys_texture.jpeg', process.env.PUBLIC_URL + '/images/titan_texture.jpeg', process.env.PUBLIC_URL + '/images/titania_texture.jpeg', process.env.PUBLIC_URL + '/images/triton_texture.jpeg', process.env.PUBLIC_URL + '/images/umbriel_texture.jpeg', process.env.PUBLIC_URL + '/images/uranus_rings.png', process.env.PUBLIC_URL + '/images/uranus_texture.jpeg', process.env.PUBLIC_URL + '/images/venus_texture.jpeg'
];

const textureLoader = new THREE.TextureLoader();
const loadingManager = new THREE.LoadingManager();

// Display a loading screen or progress bar
loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => {
  console.log(`Loading file: ${url}, ${itemsLoaded}/${itemsTotal} files loaded`);
};

const loadedTextures = {};

export function preloadTextures() {
  const promises = textureUrls.map(url => {
    return new Promise((resolve, reject) => {
      textureLoader.load(url, (texture) => {
        loadedTextures[url] = texture;
        resolve(texture);
      }, undefined, reject);
    });
  });

  return Promise.all(promises);
}

export { loadedTextures };
